import React, { Component } from 'react';
import {
  Badge,
  Button,
  Card,
  CardBody,
  CardFooter,
  CardHeader,
  Col,
  Collapse,
  DropdownItem,
  DropdownMenu,
  DropdownToggle,
  Fade,
  Form,
  FormGroup,
  FormText,
  FormFeedback,
  Input,
  InputGroup,
  InputGroupAddon,
  InputGroupButtonDropdown,
  InputGroupText,
  Label,
  Row,
} from 'reactstrap';
import Dropdown from 'react-dropdown';
import 'react-dropdown/style.css';
import Select from 'react-select';

class Forms extends Component {
  moneyType = ["Money Given", "Money Taken"]
  purposeType = ["Student","Other"]
  constructor(props) {
    super(props);
    this.state = {
      "type": null,
      "amount": "",
      "description": "",
      "enrollment_id": "",
      "purposeName": "",
      "pType":null,
      "enrollType":[]
    };
    this.handleChange = this.handleChange.bind(this);
    this.register = this.register.bind(this);
    this.handleChangeType = this.handleChangeType.bind(this);
    this.handleChangePType = this.handleChangePType.bind(this);
    this.handleChangeEnroll = this.handleChangeEnroll.bind(this);
  }
  componentDidMount(){
    this.fetchOptions()
}   
 fetchOptions(){
  var myArray = [];
    fetch('https://api.cedu.in/V2/api/getStudentEnrollment', {
      method: 'post',
      headers: {
        'Accept': 'application/json',
        'Content-Type': 'application/json'
      },
    }).then((res) => {
            return res.json();
        }).then((json) => {
           let values = json;
           console.log('dfsdf',values.response)
           this.setState({enrollType:values.response})
          //  console.log("dsf",values.response);
          //  values.response.map((option,index) => (
          //   myArray.push(option.enrollment_id)
          //  ));
          //  console.log("after",myArray)
          //  this.setState({enrollType:myArray});
          //  console.log("after set",this.state.enrollType)
          //   // this.setState({enrollType: values.response})
        });
        // console.log("my param",this.state.enrollType);
}
  handleChangePType(option) {
    this.setState({ pType: option.value });
  }
  handleChangeType(option) {
    this.setState({ type: option.value });
  }
  handleChangeEnroll(option) {
    console.log("dsfsd",option.value)
    this.setState({ enrollment_id: option.value});
      
      // this.setState({ studentInfo: event.value });
}
  handleChange(event, comingName) {
    if (comingName == 'purposeName') {
      this.setState({ purposeName: event.target.value });
    }
    if (comingName == 'description') {
      this.setState({ description: event.target.value });
    }
    if (comingName == 'amount') {
      this.setState({ amount: event.target.value });
    }
  }
  register(event) {
    fetch('https://api.cedu.in/V2/api/addMoney', {
      method: 'post',
      headers: {
        'Accept': 'application/json',
        'Content-Type': 'application/json'
      },
      body: JSON.stringify({
        type: this.state.type,
        description: this.state.description,                                                                
        amount: Number(this.state.amount),
        enrollment_id:this.state.enrollment_id,
        purposeName:this.state.purposeName,                                                   
        studentName:this.state.studentName,
      })
    }).then((Response) => Response.json())
      .then((Result) => {
        console.log(Result)
        if (Result.response_code == 200) {
          alert("Expenditure Added successfully");
          this.props.history.push("/Dashboard");                                                                                                                                                                                                                                                                                                                                                                  
        }
        else{
          console.log("this.state",this.state);
          alert('Server error !!!!!Please Try Again');
        }
          
      })
  }
  render() {
             
    let mydata = this.state.enrollType.map(function (option) {
      return { value:option.enrollment_id, label: [option.name," "," "," "," "," "," "," ", " "," ",option.enrollment_id] };
    })                                                                                                 
    return (
      <div className="animated fadeIn">
        <Row>
          <Col xs="12">                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                               
            <Card>
              <CardHeader>
                <i className="fa fa-edit"></i>Money Lent Form
                </CardHeader>
              <CardBody>
                <Form className="form-horizontal">
                <Row>
                        <Col xs="12" md="6">
                  <FormGroup>
                    <Label htmlFor="prependedInput">Money Type</Label>
                    <div className="controls">
                      <Dropdown options={this.moneyType} onChange={this.handleChangeType} value={this.state.type} />
                    </div>
                  </FormGroup>
                  </Col>
                  <Col xs="12" md="6">
                  <FormGroup>
                    <Label htmlFor="prependedInput">Purpose Type</Label>
                    <div className="controls">
                      <Dropdown options={this.purposeType} onChange={this.handleChangePType} value={this.state.pType} />
                    </div>
                  </FormGroup>
                  </Col>
                  </Row>
                  {
                    this.state.pType === 'Student' ?
                      <Row>
                        <Col xs="12" md="12">
                          <FormGroup>
                            <Label htmlFor="prependedInput">Student Info</Label>
                            <div className="controls">
                              {console.log(mydata)}
                            <Select
        name="form-field-name"
        onChange={this.handleChangeEnroll}
        labelKey='name'
        valueKey='countryCode'
        options={mydata}                  
      />
                            {/* <select className="form-control" onChange={this.handleChangeEnroll}>
          <option>---select---</option>
            {
            this.state.enrollType.map((option, index) => 
            (<option key={index} value={option.enrollment_id}>{option.enrollment_id}</option>))
            }
</select> */}
                              {/* <Dropdown options={
                                
                                // this.state.enrollType.map((option) => {option.name})
                                this.state.options.map((option, key) => <option key={key} />{option})
                                
                                } onChange={this.handleChangeEnroll} value={this.state.studentInfo}/> */}
                            </div>
                          </FormGroup>
                        </Col>
                      </Row>
                      :null
                  }
                  <Row>
                  <Col xs="12" md="6">
                      <FormGroup>
                        <Label htmlFor="prependedInput">Purpose Name</Label>
                        <div className="controls">
                          <Input id="appendedInput" size="16" type="text" onChange={(event) => { this.handleChange(event, 'purposeName') }} />
                        </div>
                      </FormGroup>
                    </Col>
                    <Col xs="12" md="6">
                      <FormGroup>
                        <Label htmlFor="prependedInput">Amount</Label>
                        <div className="controls">
                          <Input id="appendedInput" size="16" type="text" onChange={(event) => { this.handleChange(event, 'amount') }} />
                        </div>
                      </FormGroup>
                    </Col>
                  </Row>
                  <Row>
                    <Col xs="12" md="12">
                      <FormGroup>
                        <Label htmlFor="prependedInput">Description</Label>
                        <div className="controls">
                          <Input id="appendedInput" size="16" type="textarea" onChange={(event) => { this.handleChange(event, 'description') }} />
                        </div>
                      </FormGroup>
                    </Col>
                  </Row>
                  <div className="form-actions">
                    <Button type="submit" color="primary" onClick={this.register}>Save changes</Button>
                    <Button color="secondary">Cancel</Button>
                  </div>
                </Form>
              </CardBody>
            </Card>
          </Col>
        </Row>
      </div >
    );
  }
}

export default Forms;
